.app{
  min-height: 100%;
  background-color: rgb(230,230,230);
}
.wrapper{
  min-height: 100%;
}

.main-content {
  min-height: 100vh;
  overflow: hidden;
  position: relative;
  min-width: 960px;
}

.main-content:after {
  content: "";
  height: 87px;
  display: block;
  background-color: rgb(230,230,230);

}
